import React from "react";

import NavBar from "./utils/navbar";

import SortingVisualiser from "./sortingAlgorithms/sortingVisualiser";

import BinarySearch from "./searchingAlgorithms/binarySearch/binarySearch";
import LinearSearch from "./searchingAlgorithms/linearSearch/linearSearch";

import NQueensProblem from "./backTrackingAlgorithms/nQueensProblem/nQueensProblem";
import RatInAMazeProblem from "./backTrackingAlgorithms/ratInAMaze/ratInAMaze";

// Stylesheets
import "./utils/bootstrap.min.css";
import "./App.css";

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    openTab(tabId) {
        let tabs = document.getElementsByClassName("tabs");
        for (let i = 0; i < tabs.length; i++) tabs[i].style.display = "none";
        document.getElementById(tabId).style.display = "block";
        document.getElementById("bydefault").style.display = "none";
    }

    render() {
        return (
            <div className="App">
                <NavBar />
                <ul className="nav sticky-top bg-secondary">
                    <li className="nav-item">
                        <div
                            onClick={() => this.openTab("sortingTab")}
                            className="nav-link text-light"
                        >
                            Sorting
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className="dropdown">
                            <button
                                className="btn btn-secondary dropdown-toggle"
                                type="button"
                                id="backtrackingDropdown"
                                data-toggle="dropdown"
                            >
                                Backtracking
                            </button>
                            <div
                                className="dropdown-menu"
                                aria-labelledby="backtrackingDropdown"
                            >
                                <div
                                    onClick={() =>
                                        this.openTab("ratInAMazeTab")
                                    }
                                    className="dropdown-item"
                                >
                                    Rat In A Maze Problem
                                </div>
                                <div
                                    onClick={() => this.openTab("nqueensTab")}
                                    className="dropdown-item"
                                >
                                    N-Queens Problem
                                </div>
                            </div>
                        </div>
                    </li>
                    <li className="nav-item">
                        <div className="dropdown">
                            <button
                                className="btn btn-secondary dropdown-toggle"
                                type="button"
                                id="searchingDropdown"
                                data-toggle="dropdown"
                            >
                                Searching
                            </button>
                            <div
                                className="dropdown-menu"
                                aria-labelledby="searchingDropdown"
                            >
                                <div
                                    onClick={() =>
                                        this.openTab("linearSearchTab")
                                    }
                                    className="dropdown-item"
                                >
                                    Linear Search
                                </div>
                                <div
                                    onClick={() =>
                                        this.openTab("binarySearchTab")
                                    }
                                    className="dropdown-item"
                                >
                                    Binary Search
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
                <div>
                    <div id="bydefault" className="mt-2 text-center">
                        To get started click on the tab menu above
                    </div>
                    <div id="sortingTab" className="tabs">
                        <SortingVisualiser />
                    </div>
                    <div id="linearSearchTab" className="tabs">
                        <LinearSearch />
                    </div>
                    <div id="binarySearchTab" className="tabs">
                        <BinarySearch />
                    </div>
                    <div id="ratInAMazeTab" className="tabs">
                        <RatInAMazeProblem />
                    </div>
                    <div id="nqueensTab" className="tabs">
                        <NQueensProblem />
                    </div>
                </div>
            </div>
        );
    }
}
